.logo {
	display: flex;
	justify-content: space-between;
	background-image: url(https://i4.photo.2gis.com/images/branch/112/15762598704047254_ea08.jpg);
	height: 94vh;
	background-size: 100% 100%;
}
.logo div {
	display: flex;
	flex-direction: column;
	justify-content: start;
	width: 100%;
	font-size: 30px;
	/height: 8vh;
}
.logo div div {
	text-align: center;
}
.logo div div span {
	background: white;

}
.logo img{
	width: 200px;
	height: 200px;
	background-size: 100% 100%;
}