.table {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 72.6vh;
	font-size: 1.6vw;
}

.tTitle {
	display: flex;
	justify-content: space-between;
	margin-right: 3px;
}

.tTitle div {
	color: white;
	background: rgba(0, 45, 60, .8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
	text-align: center;
}

.tBody {
	display: flex;
	flex-direction: column;
	overflow: auto;
	display:block;
}

.tBodyItems {
	display: flex;
	justify-content: space-between;
	overflow: auto;
}

.tBodyItems div {
	background: #ffb930;
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
}

.tBodyItems div:nth-child(1){
	flex-grow: 3;
}
.tBodyItems div:nth-child(2){
	flex-grow: 7;
}
.tBodyItems div:nth-child(3),.tBodyItems div:nth-child(6){
	flex-grow: 1.5;
	text-align: center;
}
.tBodyItems div:nth-child(5),.tBodyItems div:nth-child(4){
	flex-grow: 1;
	text-align: center;
}

.tBodyItems div:last-child{
	display: none;
}

.tTitle div:nth-child(1){
	flex-grow: 3;
}
.tTitle div:nth-child(2){
	flex-grow: 7;
}
.tTitle div:nth-child(3),.tTitle div:nth-child(6){
	flex-grow: 1.5;
}
.tTitle div:nth-child(4),.tTitle div:nth-child(5){
	flex-grow: 1;
}





.active {
	background: #d7a637 !important;
}



.editPanel {
	display: flex;
	flex-direction: column;
}

.line {
	display: flex;
	justify-content: space-between;
}

.line input {
	margin: .3vw;
	font-size: 1.6vw;
	padding: .6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
}

.printButton {
	background: #305663;
	margin: .3vw;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}

.printButton:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}

 .line:nth-child(1) div:first-child{
	width : 50vw;
	display: flex;
	justify-content: space-between;
	position:relative;
	margin: .3vw;
	padding: 0px;
} 

.line:nth-child(1) div input{
	min-width: calc(100% - 1.5vw - 4px - 50px);
	font-size: 1.6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
	margin: 0px;
	padding: .6vw;
	padding-right: calc(50px + .3vw) ;
	size: 50px;
}


.line:nth-child(1) div button {
	position: absolute;
	width: 3.3vw;
	font-size: 1.6vw;
	height: 100%;
	left: calc(100% - 3.2vw);
	background: white;
	border: .1vw solid #305663;
	border-bottom-right-radius: 10px;
	border-top-right-radius: 10px;
	background-image: url(/Image/listBlack.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}


.line:nth-child(1) div button:hover{
	background: #305663;
	background-image: url(/Image/listWhite.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}


.line:nth-child(2) input{
	width : 39.2vw;
}
.line:nth-child(2) div input {
	margin: 0;
}


.line:nth-child(2) div:nth-child(2){
	width : 39.2vw;
	display: flex;
	justify-content: space-between;
	position:relative;
	padding: 0;
	margin: .3vw;
}

.line:nth-child(2) div button {
	position: absolute;
	width: 3.3vw;
	font-size: 1.6vw;
	height: 100%;
	left: calc(100% - 3.2vw);
	background: white;
	border: .1vw solid #305663;
	border-bottom-right-radius: 10px;
	border-top-right-radius: 10px;
	background-image: url(/Image/listBlack.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}


.line:nth-child(2) div button:hover{
	background: #305663;
	background-image: url(/Image/listWhite.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}

.dateSpan {
	width: 13vw;
	font-size: 1.6vw;
	margin: .3vw;
	margin-right: 2vw;
	display: flex;
	justify-content: center;
}

.dateSpan span {
	padding: .4vw;
	align-self: center;
}


.lineButtons {
	display: flex;
	justify-content: space-around;
	margin: .3vw;
}

.lineButtons button {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}

.lineButtons button:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}

