::-webkit-scrollbar-button {
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar-track {
background-color: white;
}

::-webkit-scrollbar-thumb {
-webkit-border-radius: 0px;
border-radius: 0px;
background-color:#305663;
}

::-webkit-scrollbar-thumb:hover{
background-color:#f0b6a3;
}

::-webkit-resizer{
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar{
width: 3px;
}

::selection {
background: #305663; /* Safari */
color: white;
}








::-webkit-input-placeholder {
	color:#305663; opacity:1;
}

:focus::-webkit-input-placeholder {
	opacity:0;
}
.buttonPrint {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	margin: .3vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}
.buttonPrint:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}
.bottomPrint {
	display: none;
}
@media print {
	.bottomPrint {
		margin-top: 10px;
		margin-left: 10px;
		font-size: 2vw;
		display: block !important;
	}
	.bottomPrint:first-child {
		display: flex !important;
		justify-content: center;
		margin-bottom: 4px;
	}
	.table {
		display: flex;
		flex-direction: column;
		width: calc(100% - 7px - 7px);
		height: 90vh;
		font-size: 1.6vw;
		margin-top: 40px;
		margin-left: 7px !important;
		margin-right: 7px !important;
	}

	.tTitle {
		display: flex;
		justify-content: space-between;
		margin: 0 !important;
	}

	.tTitle div {
		background: white !important;
		padding: .4vw;
		margin: 0 !important;
		display: inline-block;
		flex-basis: 10px;
		word-break: break-all;
		text-align: center;
		color: black;
		border-radius: 0 !important;
		border: 1px black solid;

	}

	.tBody {
		display: flex;
		flex-direction: column;
		overflow: visible !important;
		display:block;
	}

	.tBodyItems {
		display: flex;
		justify-content: space-between;
		overflow: visible !important;
	}

	.tBodyItems div {
		margin: 0 !important;
		border-radius: 0 !important;
		background: white !important;
		padding: .4vw;
		display: inline-block;
		flex-basis: 10px;
		word-break: break-all;
		color: black;
		border: 1px black solid;
	}
}