.table {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 71.6vh;
	font-size: 1.6vw;
}

.tTitle {
	display: flex;
	justify-content: space-between;
	margin-right: 3px;
}

.tTitle div {
	color: white;
	background: rgba(0, 45, 60, .8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
	text-align: center;
}

.tBody {
	display: flex;
	flex-direction: column;
	overflow: auto;
	display:block;
}

.tBodyItems {
	display: flex;
	justify-content: space-between;
	overflow: auto;
}

.tBodyItems div {
	background: rgba(255,165,0,.8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
}

.tBodyItems div:nth-child(1){
	flex-grow: 3;
}
.tBodyItems div:nth-child(2){
	flex-grow: 7;
}
.tBodyItems div:nth-child(3),.tBodyItems div:nth-child(4){
	flex-grow: 1;
	text-align: center;
}
.tBodyItems div:nth-child(5),.tBodyItems div:nth-child(6){
	text-align: center;
	flex-grow: 1;
}
.tBodyItems div:nth-child(7){
	text-align: center;
	flex-grow: 1;
}

.tBodyItems div:last-child{
	display: none;
}

.tTitle div:nth-child(1){
	flex-grow: 3;
}
.tTitle div:nth-child(2){
	flex-grow: 7;
}
.tTitle div:nth-child(3),.tTitle div:nth-child(4){
	flex-grow: 1;
}
.tTitle div:nth-child(5),.tTitle div:nth-child(6){
	flex-grow: 1;
}
.tTitle div:nth-child(7){
	flex-grow: 1;
}

.qa {
	background: white !important;
	background: rgba(0,255,0,.6) !important;
}
.qr {
	background: white !important;
	background: rgba(255,0,0,.6) !important;
}

.active {
	background: #d7a637 !important;
}



.editPanel {
	display: flex;
	justify-content: space-between;
}
.datePicker{
	width: 300px;
	display: inline-block;
}

.line {
	display: flex;
	justify-content: space-between;
}

.line input {
	margin: .3vw;
	font-size: 1.6vw;
	padding: .6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
}

.line:first-child input:first-child{
	width : 25vw;
}

.line:first-child input:nth-child(2){
	width : 50vw;
}

.line:first-child input:nth-child(3){
	width : 20vw;
}

.line:nth-child(2) input:nth-child(1){
	width : 20vw;
}

.line:nth-child(2) div{
	width : 38vw;
	display: flex;
	justify-content: space-between;
	position:relative;
	margin: .3vw;
	padding: 0px;
}

.line:nth-child(2) div input{
	min-width: calc(100% - 1.5vw - 4px - 50px);
	font-size: 1.6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
	margin: 0px;
	padding: .6vw;
	padding-right: calc(50px + .3vw) ;
	size: 50px;
}


.line:nth-child(2) div button {
	position: absolute;
	width: 3.3vw;
	font-size: 1.6vw;
	height: 100%;
	left: calc(100% - 3.2vw);
	background: white;
	border: .1vw solid #305663;
	border-bottom-right-radius: 10px;
	border-top-right-radius: 10px;
	background-image: url(/Image/listBlack.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}


.line:nth-child(2) div button:hover{
	background: #305663;
	background-image: url(/Image/listWhite.png);
	background-size: 80% 80%;
	background-repeat: no-repeat;
	background-position: center;
	transition: .3s;

}

.panelButton {
	display: flex;
	justify-content: flex-end;
	margin: .3vw;
}

.panelButton button {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}

.panelButton button:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}





::-webkit-scrollbar-button {
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar-track {
background-color: white;
}

::-webkit-scrollbar-thumb {
-webkit-border-radius: 0px;
border-radius: 0px;
background-color:#305663;
}

::-webkit-scrollbar-thumb:hover{
background-color:#f0b6a3;
}

::-webkit-resizer{
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar{
width: 3px;
}

::selection {
background: #305663; /* Safari */
color: white;
}


.bottomPrint {
	display: none;
}

@media print {
	.bottomPrint {
		margin-top: 10px;
		margin-left: 10px;
		font-size: 2vw;
		display: block !important;
	}
	.bottomPrint:first-child {
		display: flex !important;
		justify-content: center;
	}
	.table {
		display: flex;
		flex-direction: column;
		width: calc(100% - 7px - 7px);
		height: 90vh;
		font-size: 1.6vw;
		margin-top: 40px;
		margin-left: 7px !important;
		margin-right: 7px !important;
	}

	.tTitle {
		display: flex;
		justify-content: space-between;
		margin: 0 !important;
	}

	.tTitle div {
		background: white !important;
		padding: .4vw;
		margin: 0 !important;
		display: inline-block;
		flex-basis: 10px;
		word-break: break-all;
		text-align: center;
		color: black;
		border-radius: 0 !important;
		border: 1px black solid;

	}

	.tBody {
		display: flex;
		flex-direction: column;
		overflow: auto;
		display:block;
	}

	.tBodyItems {
		display: flex;
		justify-content: space-between;
		overflow: auto;
	}

	.tBodyItems div {
		margin: 0 !important;
		border-radius: 0 !important;
		background: white !important;
		padding: .4vw;
		display: inline-block;
		flex-basis: 10px;
		word-break: break-all;
		color: black;
		border: 1px black solid;
	}
}




::-webkit-input-placeholder {
	color:#305663; opacity:1;
}

:focus::-webkit-input-placeholder {
	opacity:0;
}