.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background: black;
	opacity: .5;
	display: none;
}

.popup {
	position: absolute;
	top: 50%;
	left: 50%;
	background: white;
	width: 96%;
	height: 96vh;
	transform: translate(-50%, -50%);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border: 2px solid #305663;
	border-radius: 10px;
	filter: none !important;
	display: none;

} 
.modalBody{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;

}
.topMenu {
	height: 4vh;
	padding: .5vh;
	border-bottom: 2px solid #305663;
	display: flex;
	justify-content: space-between;
}

.modalTitle{
	width: 100%;
	font-size: 1.6vw;
	display: flex;
	justify-content: center;
}
.modalTitle div{
	align-self: center;
}

.closeButton {
	height: 100%;
	width: 4vw;
}
.closeButton div {
	width: 100%;
	height: 100%;
	background-image: url('/Image/closeGreen.svg');
	background-size: 100% 100%;
}
.closeButton div:hover {
	width: 100%;
	height: 100%;
	background-image: url('/Image/closeOrange.svg');
	background-size: 100% 100%;	
}

.bottomMenu {
	height: 7vh;
	border-top: 2px solid #305663;
	display: flex;
	justify-content: center; 
	width: 100%;

}

.bottomMenu button {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
	align-self: center;
	margin-right: 5px;
}

.bottomMenu button:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}


.table {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 83vh;
	font-size: 1.6vw;
}

.tTitle {
	display: flex;
	justify-content: space-between;
	margin-right: 3px;
}

.tTitle div {
	color: white;
	background: rgba(0, 45, 60, .8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
	text-align: center;
}

.tBody {
	display: flex;
	flex-direction: column;
	overflow: auto;
	display:block;
}

.tBodyItems {
	display: flex;
	justify-content: space-between;
	overflow: auto;
}

.tBodyItems div {
	background: #ffb930;
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
}

.tBodyItems div:nth-child(1){
	flex-grow: 4;
}
.tBodyItems div:nth-child(2){
	flex-grow: 8;
}
.tBodyItems div:nth-child(3){
	text-align: center;
	flex-grow: 1;
}
.tBodyItems div:nth-child(4){
	flex-grow: 1;
	text-align: center;
}
.tBodyItems div:nth-child(5){
	text-align: center;
	flex-grow: 1.5;
}
.tBodyItems div:nth-child(6){
	text-align: center;
	flex-grow: 1.5;
}
.tBodyItems div:nth-child(7){
	text-align: center;
	flex-grow: 1.5;
}
.tBodyItems div:last-child{
	display: none;
}

.tTitle div:nth-child(1){
	flex-grow: 4;
}
.tTitle div:nth-child(2){
	flex-grow: 8;
}
.tTitle div:nth-child(3){
	flex-grow: 1;
}
.tTitle div:nth-child(4){
	flex-grow: 1;
}
.tTitle div:nth-child(5){
	flex-grow: 1.5;
}
.tTitle div:nth-child(6){
	flex-grow: 1.5;
}
.tTitle div:nth-child(7){
	flex-grow: 1.5;
}

.active {
	background: #d7a637 !important;
}

