.main{
	display: flex;
	justify-content: center;
	height: 100vh;
}
.form {
	display: flex;
	justify-content: space-around;
	flex-direction: column;
	align-self: center;
}

.form input {
	margin: .3vw;
	font-size: 1.6vw;
	padding: .6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
}

.form button {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	margin: .3vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}

.form button:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}
