* {
	margin: 0;
	padding: 0;
}
.mainUl, .subUl {
	display: flex;
	background: #305663;
	color: white;
	font-size: 1.6vw;
}

.mainUl li {
	list-style: none;
	position: relative;
}
.mainUl li div {
	transition: .3s;
	padding: .4rem;
	cursor: pointer;
	padding-left: 2.5rem;
	padding-right: 2.5rem;

}
.mainUl li div:hover{
	background: #ffb930;
	color: #305663;
	transition: .3s;
}

.mainUl li:hover > ul{
	display: block;
	transition: .3s;
}

.mainUl li a {
	text-decoration: none;
	color: white;

}

.subUl {
	position: absolute;
	min-width: 10vw;
	display: flex;
	flex-direction: column;
	font-size: 1.6vw;
	display: none;
	transition: 1s;
	z-index: 100;
}
.subUl li {
	text-align: start;
	border-top: 1px solid #ffb930;
	z-index: 100;

}
.subUl li:hover {
	border-top: 1px solid #305663;
}
.subUl li a {
	z-index: 100;
	text-decoration: none;
	color: white;

}


@media screen and (max-width: 760px){
	.mainUl, .subUl {
		flex-direction: column;
	}
	.subUl {
		position: relative;
	}
	.subUl li div{
		padding-left: 80px;
	}
}
