.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background: black;
	opacity: .5;
	display: none;
}

.popup {
	position: absolute;
	top: 50%;
	left: 50%;
	background: white;
	transform: translate(-50%, -50%);
	border: 2px solid #305663;
	border-radius: 10px;
	filter: none !important;
	display: none;
	padding: 1vw;

} 
.modalBody{
	width: 100% ;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 1.6vw;
	word-break: break-all;
}