
.table {
	margin-left: 15%;
	display: flex;
	flex-direction: column;
	width:70%;
	height: 78vh;
	font-size: 1.6vw;
}

.tTitle {
	display: flex;
	justify-content: space-between;
	margin-right: 3px;
}

.tTitle div {
	color: white;
	background: rgba(0, 45, 60, .8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
	text-align: center;
}

.tBody {
	display: flex;
	flex-direction: column;
	overflow: auto;
	display:block;
}

.tBodyItems {
	display: flex;
	justify-content: space-between;
	overflow: auto;
}

.tBodyItems div {
	background: rgba(255,165,0,.8);
	margin: .2vw;
	border-radius: 10px;
	padding: .4vw;
	display: inline-block;
	flex-basis: 10px;
	word-break: break-all;
}

.tBodyItems div:nth-child(1){
	flex-grow: 1;
	text-align: center;
}
.tBodyItems div:nth-child(2){
	flex-grow: 8;
}

.tTitle div:nth-child(1){
	flex-grow: 1;
}
.tTitle div:nth-child(2){
	flex-grow: 8;
}

.active {
	background: #d7a637 !important;
}

.editPanel {
	display: flex;
	flex-direction: column;
}

.line {
	display: flex;
	justify-content: center;
}


.line input {
	margin: .3vw;
	font-size: 1.6vw;
	padding: .6vw;
	border-radius: 10px;
	border: #305663 .1vw solid;
	color: black;
}

.line:first-child input:first-child{
	width : 65vw;
}

.lineButtons {
	display: flex;
	justify-content: space-around;
	margin: .3vw;
}

.lineButtons button {
	background: #305663;
	width: 30vw;
	font-size: 1.6vw;
	padding: .6vw;
	border: .1vw solid #305663;
	border-radius: 10px;
	color: white;
	transition: .3s;
}

.lineButtons button:hover {
	color: #305663;
	background: #ffb930;
	transition: .3s;
}

::-webkit-scrollbar-button {
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar-track {
background-color: white;
}

::-webkit-scrollbar-thumb {
-webkit-border-radius: 0px;
border-radius: 0px;
background-color:#305663;
}

::-webkit-scrollbar-thumb:hover{
background-color:#f0b6a3;
}

::-webkit-resizer{
background-repeat:no-repeat;
width:3px;
height:0px
}

::-webkit-scrollbar{
width: 3px;
}

::selection {
background: #305663; /* Safari */
color: white;
}
